<template>
	<div ref="container" :class="$style.container">
    <BaseEventList
      :events="eventListing"
      v-show="showEventListing"
    />
    <FullCalendar
      ref="fullCalendar"
      :plugins="plugins"
      :events="events"
      defaultView="dayGridMonth"
      height="auto"
      @dateClick="handleDateClick"
      :first-day="1"
      :event-limit="2"
      :text-escape="false"
      :event-limit-text="eventLimitText"
      :event-limit-click="handleEventLimitClick"
      :event-render="eventRender"
      next-day-threshold="24:00:00"
      :editable="true"
      :button-text="{
        today: 'Today'
      }"
      :header="{
        left: 'prev title next',
        right: 'today'
      }"
    />
	</div>
</template>

<script>
/*
    var calendarEl = document.getElementById('calendar');
    var calendar = new FullCalendar.Calendar(calendarEl, {
      plugins: [ 'dayGrid', 'timeGrid', 'list' ],
    });

*/

import FullCalendar from '@fullcalendar/vue'
import dayGridPlugin from '@fullcalendar/daygrid'
import interactionPlugin from '@fullcalendar/interaction'
import listPlugin from '@fullcalendar/list'
import BaseEventList from './BaseEventList.vue'
export default {
  data() {
    return {
      plugins: [ dayGridPlugin, interactionPlugin, listPlugin],
      currentDate: null
    }
  },
  props: {
    events: {
      type: Array,
      validator (val) {
        return Array.isArray(val)
          && val.every(event => event.hasOwnProperty('title') && event.hasOwnProperty('date') )
      },
      default: () => []
    }
  },
	components: {
		FullCalendar,
    BaseEventList
	},
  computed: {
    eventListing() {
      return this.events.filter( event => event.date.includes(this.currentDate))
    },
    showEventListing() {
      return this.eventListing.length > 0
    }
  },
  watch: {
    currentDate() {
      if(this.showEventListing) {
        const y = this.$refs.container.getBoundingClientRect().top + window.pageYOffset
        window.scrollTo(0, y)
      }
    }
  },
  methods: {
    handleEventLimitClick: function(info) {
      this.currentDate = info.date.getFullYear() + '-' + ('0' + (info.date.getMonth()+1)).slice(-2) + '-' + ('0' + (info.date.getDate())).slice(-2)
    },
    handleDateClick: function(info) {
      this.currentDate = info.dateStr
      // const calendar = this.$refs.fullCalendar.getApi()
    },
    eventRender: function (info) {
      const date = info.event.start.getFullYear() + '-' + ('0' + (info.event.start.getMonth()+1)).slice(-2) + '-' + ('0' + (info.event.start.getDate())).slice(-2)
      const days = this.$refs.container.querySelectorAll(`[data-date="${date}"]`)
      days.forEach(day => {
        day.classList.add('events-active')
        if (info.event.extendedProps.featured) {
          day.classList.add('featured')
        }
      })
      /*
      // --------------------------------------------------------- //
      // Add Active Events Flag and Featured Flag to Calendar Date
      // --------------------------------------------------------- //
      const $ = this.$refs.container.querySelectorAll
      var tempFeatureDate = eventobj.event.start.getFullYear() + '-' + ('0' + (eventobj.event.start.getMonth()+1)).slice(-2) + '-' + ('0' + (eventobj.event.start.getDate())).slice(-2);
      var $dayNumber = $('[data-date="' + tempFeatureDate + '"]');

      $dayNumber.addClass("events-active");

      if (eventobj.event.classNames == "featured") {
        $dayNumber.addClass("featured");
      }

      // --------------------------------------------------------- //
      // Add Legend in Header
      // --------------------------------------------------------- //
      if ($('.legend').length == 0) {
        $('.fc-today-button').before('<div class="legend">Featured event</div>');
      }
      */
    },
    eventLimitText(eventCount) {
      if (eventCount > 1) {
        return "View " + eventCount + " more events";
      }
      else {
        return "View " + eventCount + " more event";
      }
    },
  },
}
/*
    $('.fc-button-primary').attr('tabindex', '0');
    */
</script>

<style lang='scss' module>
.container {
  position: relative;
  font-family: $font-text;
}
</style>

<style lang='scss'>
@import '~@fullcalendar/core/main.css';
@import '~@fullcalendar/daygrid/main.css';
@import '../assets/styles/fullcalendar-custom.css';

.events-active::after {
  content: "\2022" / "";
  font-size: 30px;
  left: 0;
  line-height: 0;
  margin: 0 auto;
  position: absolute;
  right: 0;
  text-align: center;
  top: 60px;
  z-index: 1;
}
</style>

