<template>
  <div ref="calendar-events-wrapper" class="events-wrapper">
    <h2 class="date-header">{{date}} events</h2>
    <div v-for="event in eventList" :key="event.title + event.date" class="event-list-wrapper">
      <div class="event-list__time">{{event.time}}</div>
      <div class="event-list__content">
        <div class="event-list__title">{{event.title}}</div>
        <div class="event-list__description" >{{event.description}}</div>
        <a :href="event.url" class="event-list__link"></a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
props: {
  events: {
    type: Array,
    default: () => []
  }
},
computed: {
  eventList() {
    return this.events.map(event => {
      const date = new Date(event.date)
      const time = date.toLocaleTimeString([], {hour: '2-digit', minute:'2-digit'}).replace('AM', 'a.m.').replace('PM', 'p.m.')
      return {
        ...event,
        time
      }
    })
  },
  firstEvent() {
    return this.events[0]
  },
  date() {
    if(!this.firstEvent) return ''
      const monthNames = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"]
      const dayNames = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"]
      const start = new Date(this.firstEvent.date)
      return dayNames[start.getDay() - 1] + ', ' +
            monthNames[start.getMonth()] + ' ' +
            start.getDate() + nth(start.getDate()) + ', ' +
            start.getFullYear()
  }
}
}
   // ------------------------------------ //
    // Date Ordinal (st, nd, rd, th)
    // ------------------------------------ //
    function nth(d) {
      if (d > 3 && d < 21) return 'th';
      switch (d % 10) {
        case 1:  return "st";
        case 2:  return "nd";
        case 3:  return "rd";
        default: return "th";
      }
    }
</script>

